/* Start Global Rules */
* {
  padding: 0;
  margin: 0;
}
*,
::before,
::after {
  box-sizing: border-box;
}
body {
  font-family: 'League Spartan', sans-serif;
  /* Temp */
  background-color: rgb(44, 44, 44);
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
/* Start Global Rules */
/* Start Header */
.main .header {
  display: flex;
  align-items: center;
  gap: 35px;
  height: 70px;
  padding-inline: 50px;
  position: relative;
  z-index: 10;
}
@media (max-width: 991px) {
  .main .header {
    justify-content: space-between;
  }
}
.main .header .list {
  display: flex;
  align-items: center;
  gap: 20px;
}
@media (max-width: 991px) {
  .main .header .list {
    display: none;
  }
}
.main .header .list li a {
  color: white;
  letter-spacing: 1px;
  position: relative;
}
.main .header .list li a::before {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  background-color: white;
  left: 50%;
  transform: translateX(-50%);
  bottom: -4px;
  transition: width 0.3s;
}
.main .header .list li a:hover::before {
  width: 100%;
}
.main .header .burger-menu {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 5px;
  display: none;
}
@media (max-width: 991px) {
  .main .header .burger-menu {
    display: block;
  }
}
.main .header .burger-menu > svg {
  color: white;
  font-size: 2rem;
}
.main .header .mini-list {
  position: absolute;
  width: 100%;
  left: 0;
  /* Temp */
  top: -150px;
  background-color: white;
  display: flex;
  height: 150px;
  justify-content: space-around;
  align-items: center;
  transition: top 0.3s;
}
.main .header .mini-list-active {
  top: 0;
}
.main .header .mini-list .close-menu {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 2rem;
}
.main .header .mini-list .close-menu svg {
  color: #c9c9c9;
  transition: color 0.3s;
}
.main .header .mini-list .close-menu svg:hover {
  color: black;
}
.main .header .mini-list .mobile-list {
  display: flex;
  align-items: center;
  gap: 30px;
}
.main .header .mini-list .mobile-list li a {
  color: black;
  font-size: 1.4rem;
  padding: 10px;
  border-radius: 10px;
  transition: background-color 0.3s;
}
@media (max-width: 500px) {
  .main .header .mini-list .mobile-list li a {
    padding: 0;
    font-size: 1.2rem;
  }
}
.main .header .mini-list .mobile-list li a:active {
  background-color: #c9c9c9;
}
/* End Header */
/* Start Content */
.main .content {
  background-color: white;
  height: 100vh;
  margin-top: -70px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 991px) {
  .main .content {
    height: auto;
  }
}
.main .content .top {
  height: 65vh;
}
.main .content .bottom {
  height: 35vh;
}
@media (max-width: 991px) {
  .main .content .top {
    height: auto;
  }
  .main .content .bottom {
    height: auto;
  }
}
/* start top */
.main .content .top .box {
  display: none;
}
.main .content .top .box-active {
  display: flex;
}
@media (max-width: 991px) {
  .main .content .top .box-active {
    flex-direction: column;
  }
}
.main .content .top .box .left {
  width: 60vw;
  height: 65vh;
  position: relative;
}
@media (max-width: 991px) {
  .main .content .top .box .left {
    width: 100vw;
    height: auto;
  }
}
.main .content .top .box .left .switch {
  position: absolute;
  right: -140px;
  bottom: 0;
}
@media (max-width: 991px) {
  .main .content .top .box .left .switch {
    right: 0px;
  }
}
.main .content .top .box .left .switch > button {
  position: relative;
  background-color: black;
  border: none;
  height: 70px;
  width: 70px;
  color: #a4a4a4;
  font-size: 2rem;
  cursor: pointer;
  transition: background-color 0.3s;
}
.main .content .top .box .left .switch > button:hover {
  background-color: #444444;
}
.main .content .top .box .left .switch > button svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.main .content .box .left > img {
  width: 100%;
  height: 100%;
}
.main .content .box .right {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 40px;
}
@media (max-width: 991px) {
  .main .content .box .right {
    width: 100%;
    text-align: center;
  }
}
.main .content .box .right h1 {
  font-size: 2.5rem;
  font-weight: 500;
  margin-bottom: 20px;
}
.main .content .box .right p {
  color: #979595;
  margin-bottom: 20px;
}
.main .content .box .right a {
  gap: 30px;
  display: flex;
  align-items: center;
  color: black;
  text-transform: uppercase;
  letter-spacing: 7px;
  font-weight: 500;
  transition: color 0.3s;
}
.main .content .box .right a:hover {
  color: #b6b6b6;
}

/* End top */
/* Start Bottom */
.main .content .bottom {
  display: flex;
}
@media (max-width: 991px) {
  .main .content .bottom {
    flex-direction: column;
  }
}
.main .content .bottom > img {
  width: 25vw;
}
.main .content .bottom > .text {
  width: 50vw;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
}
@media (max-width: 991px) {
  .main .content .bottom > img {
    width: 100%;
  }
  .main .content .bottom > .text {
    width: 100%;
  }
}
.main .content .bottom > .text > h2 {
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 4px;
}
.main .content .bottom > .text > p {
  color: #979595;
}

/* End Bottom */
/* End Content */
